body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  height: 100%;
  width: 100%;
}

.App {
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.twitter-timeline {
  margin-left: 5em;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.videoCover {
  position: fixed;
  z-index: -99;
  width: 100%;
  height: 100%;
}

video { 
  position: fixed;
  top: 35%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  background-size: cover;
  -webkit-transition: 1s opacity;
  transition: 1s opacity;
}
.contentArea {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    z-index: 1;
    background-color: #e6ecf0;
    margin-top: 35em;
}
.profileAvatar {
    border-radius: 50%;
    border: 0.5em solid white;
    margin-top: -50%;
    margin-left: 50%;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.nameSection {
    width: 20%;
    margin-left: 65%;
    min-width: 15em;
    height: auto;
    padding: 1em;
  }
  
  .nameSection-name {
    color: black;
    font-weight: 700;
    font-size: 1.75em;
  }
  
  .nameSection-tag {
    font-size: 1em;
    color: #5f5b5b;
  }
.profileAvatarContainer {
  width: 20vw;
  min-width: 12em;
  min-height: 12em;
  max-width: 20em;
  max-height: 20em;
  z-index: 2;
  position: absolute;
}
.tabContainer {
  width: 100%;
  height: 5em;
  background-color: white;
  z-index: 1;
  display: flex;
  justify-content: flex-end;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.tabContainer-tabArea {
  display: flex;
  width: 70%;
}
.innerContentArea {
  width: 75%;
  min-width: 40em;
  margin-top: 4em;
  margin-left: 11%;
  margin-right: 11%;
  padding: 1em;
  display: flex;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.descriptionSection {
  margin-top: 7em;
}
.descriptionSectionRow {
  display: block;
  color: #515154;
  font-size: 1.2em;
}

.descriptionSectionRow img {
  display: inline;
  height: 1.2em;
}

.descriptionSectionRow a {
  display: inline;
  text-decoration: none;
  color: #1681df;
}

.descriptionSectionRow a:visited {
  text-decoration: none;
  color: #1681df;
}
.footer {
  background-color: rgb(20, 19, 19);
  height: 11.1em;
}

.footer img {
  float: right;
}
.tab {
  display: flex;
  flex-direction: column;
  color: #494848;
  padding: 1.5em 3em;
  cursor: pointer;
}

.tab-icon {
  width: 100%;
  justify-self: center;
  font-size: 1.5em;
}

.tab:hover {
  color: #137ebd;
  /* border-left: 1px solid #494848;
  border-right: 1px solid #494848; */
  box-shadow: inset 0px 0px 100px #cac8c8;
}
