.videoCover {
  position: fixed;
  z-index: -99;
  width: 100%;
  height: 100%;
}

video { 
  position: fixed;
  top: 35%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
  background-size: cover;
  transition: 1s opacity;
}