.tab {
  display: flex;
  flex-direction: column;
  color: #494848;
  padding: 1.5em 3em;
  cursor: pointer;
}

.tab-icon {
  width: 100%;
  justify-self: center;
  font-size: 1.5em;
}

.tab:hover {
  color: #137ebd;
  /* border-left: 1px solid #494848;
  border-right: 1px solid #494848; */
  box-shadow: inset 0px 0px 100px #cac8c8;
}